html {
  height: 100%;
  margin: 0;
  overflow-x: hidden;
  overflow-y: scroll;
}

body {
  height: 100%;
  margin: 0;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
  margin: 0;
}

div {
  display: flex;
  flex: 1 1 100%;
}

code {
  font-family: Inconsolata, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.App_app__12qws {
  margin: 0;
  display: flex;
  flex: 1 1 100%;
  overflow: hidden;
  align-items: stretch;
}
:root {
  --fg: black;
  --bg: white;
  --input-fg: var(--fg);
  --input-bg: rgba(100,100,100,0.05);
  --display-fg: black;
  --display-bg: white;
  --error-fg: #777;
  --sansfont: "open sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  --monofont: Inconsolata, Menlo, Monaco, Consolas, "IBM Plex Mono", "Source Code Pro", 
  "Courier New", monospace;
}

button {
  color: black;
  color: var(--fg);
  font-size: .8rem;
  align-self: baseline;
}

.FormulaEditor_formulaWrapper__2MAe0 {
  color: black;
  color: var(--fg);
  background-color: white;
  background-color: var(--bg);
  margin: 0;
  display: grid;
  padding: 0.5rem;
  grid-template-areas:
    "title"
    "options"
    "input"
    "display";
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, auto) 1fr;
  overflow: hidden;
}

.FormulaEditor_title__DKEy- {
  grid-area: title;
  font-size: 1.5rem;
  font-weight: bold;
}

.FormulaEditor_formulaOptions__25AG7 {
  grid-area: options;
  display: flex;
  flex-direction: row;
  flex: 0 1 auto;
  margin: 0.2rem 0;
}

.FormulaEditor_formulaOptions__25AG7 > * {
  margin-right: 1rem;
  flex: 0 1 auto;
}

.FormulaEditor_formulaOptions__25AG7 > * > * {
  flex: 0 1 auto;
}

.FormulaEditor_formulaInput__11beu {
  grid-area: input;
}

textarea {
  font-size: 1.2rem;
  border: 2px solid black;
  border: 2px solid var(--fg);
  color: black;
  color: var(--input-fg);
  background-color:  rgba(100,100,100,0.05);
  background-color:  var(--input-bg);
  font-family: Inconsolata, Menlo, Monaco, Consolas, "IBM Plex Mono", "Source Code Pro", 
  "Courier New", monospace;
  font-family: var(--monofont);
  outline: none;
}

textarea:focus {
  font-weight: bold;
}

.FormulaEditor_formulaDisplayWrapper__323Cl {
  grid-area: display;
  overflow: scroll;
  background-color: white;
  background-color: var(--display-bg);
  margin-top: 0.4rem;
}

.FormulaEditor_formulaDisplay__2Zm9A {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0.3rem;
  color: black;
  color: var(--display-fg);
}

.FormulaEditor_formulaDisplayError__1dchW {
  padding: .2rem .4rem;
  grid-area: display;
  font-family: Inconsolata, Menlo, Monaco, Consolas, "IBM Plex Mono", "Source Code Pro", 
  "Courier New", monospace;
  font-family: var(--monofont);
  color: #777;
  color: var(--error-fg);
}
